.header {
  width: 100%;
  padding: 30px; /* Adequate padding for spacing */
  background: linear-gradient(to bottom, #6a0dad, #ff69b4);
  display: flex;
  flex-direction: column; /* Stack title above nav */
  justify-content: center;
  align-items: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: auto; /* Fix header at the top */
    top: 0; /* Pin it to the top */
  left: 0; /* Ensure it starts from the left */
  z-index: 201; /* Higher z-index to prevent overlap by other elements */
  min-width: 239.8vh;
}
  
.image-page {
  width: 100%;
  min-width: 100vh;
  margin-left: 0vh;
    margin-top: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    right: 0px;
    left: 0px;
    padding: 0px 3600px;
    background: linear-gradient(180deg, #eba4ff, #8f0788);
    height: 102%;
    min-height: 120vh;
    color: white;
    text-align: center;
    overflow-x: hidden;
    z-index: 200;
  }
  .home-page5{
      width: 80%;
        margin-top: 340px;
        display: flex;
        flex-direction: column;
        align-items: center;
        right: 0px;
        left: 0px;
        padding: 50px 3000px;
        background: linear-gradient(180deg, #481257, #550451);
        min-height: 100vh;
        color: white;
        text-align: center;
        overflow-x: hidden;
        z-index: 201;
      }
  .home-page5 .image-page{
    width: 80%;
    background-size: cover; /* Ensures background resizes properly */
  background-position: center;
    margin-top: 240px;
    display: flex;
    flex-direction: column;
    align-items: center;
    right: 0px;
    left: 0px;
    padding: 0px 3000px;
    background: linear-gradient(180deg, #481257, #550451);
    min-height: 100vh;
    color: white;
    text-align: center;
    overflow-x: hidden;
    z-index: 201;

  }
  
  
  .carousel-header2 h1 {
    font-family: "Shuttle Block", sans-serif;
    font-size: 40px;
    color: hwb(0 100% 0%);
    background: linear-gradient(90deg, #3d0e54, #f971c7);
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
    padding: 20px;
    border-radius: 5px;
    margin-bottom: 40px;
    z-index: 200px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  .page-title {
    margin-bottom: -10px;
    font-family: 'Shuttle Block', sans-serif; /* Matches the font family */
    font-style: normal; /* Remove italic styles if present */
    font-weight: bold; /* Matches the bold style */
    font-size: 39px; /* Matches the font size */
    color: hwb(0 100% 0%); /* Matches the text color */
    border: 1px dashed rgb(250, 188, 223);    
    box-shadow: 0 0 5px rgba(243, 164, 255, 0.5); /* Shadow around the header */
    padding: 15px;
    margin-bottom: 20px;
    border-radius: 10px;
  }
  
  
  .carousel-container2 {
    margin-top: 80px;
    position: relative;
    width: 390px;
    height: 488px;
    margin: 20px auto;
    display: flex;
    align-items: center;
    justify-content: center;
    background:transparent;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    z-index: 200;
  }
  
  .carousel-image2 {
    height: 100%;
    width: 100%;
    min-width: 390px;
    min-height: 844px;
    background-size: cover;
    background-position: center;
    border-radius: 10px;
    transition: transform 0.5s ease-in-out;
  }
  
  .carousel-button2 {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    font-size: 24px;
    z-index: 10;
    border-radius: 50%;
  }
  .carousel-title2 {
    font-family: "Shuttle Block", sans-serif;
    font-size: 30px;
    color: hwb(0 100% 0%);
    margin-bottom: 20px;
    text-align: center;
  }
  
  .carousel-button2.left {
    left: 10px;
  }
  
  .carousel-button2.right {
    right: 10px;
  }
  
  .download-options2 {
    height: 100%;
    min-height: 10vh;
    display: flex;
    gap: 15px;
    margin-left:440px;
    margin-top: 10px;
  }
  
  .download-button2 {
    margin-top: 20px;
    padding: 10px 20px;
    background: linear-gradient(90deg, #8605b1, #ff86dd);
    color: white;
    border: none;
    border-radius: 5px;
    text-decoration: none;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    font-family: "Shuttle Block", sans-serif;
    transition: background-color 0.3s ease;
  }
  
  .download-button2:hover {
    background: #ff85c2;
  }
  
  .download-button2.locked {
    background: #6a0dad;
    cursor: not-allowed;

  }
  
  .download-button2.locked:hover {
    background: #8a2be2;
  }
  html,body {
    height: 100%; /* Make sure html and body cover the full height */
    width: 100%;
    padding: 0;

  }
  .carousel2 {
    margin: 20px auto;
    width: 80%;
    min-height: 60vh;
    background: linear-gradient(90deg, #8605b1, #ff86b4);
    border: 3px solid #ffffff;
    padding: 10px;
    border-radius: 8px;
  }
  
  .responsive-carousel .carousel .slide img {
    max-height: 400px;
    object-fit: contain;
    justify-content: center;

  }
  .carousel-photo-wrapper {
    display: flex;
    justify-content: center;
    height: 400px;
    width:100%;
    background-color: transparent;
    border: 0px solid #ddd;
  }
  .vector8{
    display: none;
  }
  .vector9{
    display: none;
  }

/* Responsive Adjustments for Tablets and Phones */
@media (max-width: 1024px) { /* Tablets */
  .header {
    padding: 20px;
    min-width: 100%;
    flex-direction: column;
    text-align: center;
  }

  .image-page,
  .home-page5 {
    padding: 20px;
    min-width: 100%;
    margin-top: 100px;
  }

  .carousel-header2 h1,
  .page-title {
    font-size: 24px;
    padding: 10px;
    margin-bottom: 15px;
  }

  .carousel-container2 {
    width: 300px;
    height: 400px;
  }

  .carousel-image2 {
    width: 100%;
    min-width: 300px;
    min-height: 400px;
  }

  .carousel-button2 {
    font-size: 20px;
    padding: 8px;
  }

  .download-options2 {
    margin-left: 0;
    flex-direction: column;
    gap: 10px;
    align-items: center;
  }

  .download-button2 {
    width: 80%;
    font-size: 14px;
  }

  .carousel2 {
    padding: 5px;
    width: 90%;
  }

  .carousel-photo-wrapper {
    height: 300px;
  }

  .vector8{
    display: none; /* Hide vectors for phones */

  }
  .vector9 {
    display: none; /* Hide vectors for phones */

  }
}

@media (max-width: 692px) { /* Phones */
  .header {
    padding: 15px;
    min-width: 100%;
    width: 395%;
    height: 60%;
    flex-direction: column;
    text-align: center;
  }

  .image-page{
    margin-top: 0px;
    height: 100%;
    min-height: 600vh;
    margin-top: 420px;
    width: 70%;
    background-color: transparent;
    min-width: 10vh;
  }
  .home-page5{
    margin-top: 0px;
    height: 300%;
    min-height: 420vh;
    margin-top: 420px;
    width: 80%;
    min-width: 10vh;
  }
  .home-page5, .image-page {
    padding: 10px;
    width: 100%;
    align-items: center;
    margin-left: 50px;
    min-width: 100vh;
    background-color: transparent;
    height: 390%;
    min-height: 190vh;
    margin-top: 905px;
  }

  .carousel-header2 h1,
  .page-title {
    font-size: 20px;
    padding: 8px;
    margin-bottom: 10px;
  }

  .carousel-container2 {
    width: 250px;
    margin-top: 20px;
    height: 500px;
  }

  .carousel-image2 {
    width: 100%;
    min-width: 250px;
    min-height: 300px;
  }

  .carousel-button2 {
    font-size: 18px;
    padding: 6px;
  }

  .download-options2 {
    margin-left: 0;
    flex-direction: column;
    gap: 8px;
    width: 80%;
    margin-left: 120px;
    align-items: center;
  }

  .download-button2 {
    width: 100%;
    font-size: 12px;
    padding: 8px;
  }

  .carousel2 {
    padding: 5px;
    width: 95%;
  }

  .carousel-photo-wrapper {
    margin-top: 100px;
    height: 450px;
  }

  .vector8{
    display: none; /* Hide vectors for phones */

  }
  .vector9 {
    display: none; /* Hide vectors for phones */
  }
}
