#volume {
  margin-top: 15px;
  width: 150px;
  -webkit-appearance: none;
  appearance: none;
  height: 6px;
  background: #ddd;
  border-radius: 5px;
  outline: none;
  transition: background 0.2s ease;
}

#volume::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 15px;
  height: 15px;
  background: #6a0dad;
  border-radius: 50%;
  cursor: pointer;
  transition: background 0.2s ease;
}

#volume::-webkit-slider-thumb:hover {
  background: #8a2be2;
