/* Style for the title box (legend) */
/* Style for the title box (legend) as a wide rectangle */
.legend-download {
    display: flex; /* Ensures content aligns properly */
    justify-content: center; /* Center text horizontally */
    align-items: center; /* Center text vertically */
    background: rgba(0, 0, 0, 0.8); /* Button background color */
    color: white; /* Text color */
    font-size: 14px; /* Text size */
    text-align: center;
    padding: 5px 0; /* Minimal height with vertical padding */
    width: 70%; /* Adjust width as needed */
    margin-bottom: 40px; /* Center the button horizontally */
    margin-left: 90px;
    border-radius: 4px; /* Rounded corners for a modern look */
    cursor: pointer; /* Pointer cursor to indicate interactivity */
    text-decoration: none; /* Remove underline from link */
    transition: background 0.3s ease; /* Smooth hover transition */
    z-index: 300; 
  }
  
  /* Hover effect */
  .legend-download:hover {
    background: rgba(0, 144, 10, 0.9); /* Slightly darker on hover */
    margin-bottom: 30px; /* Center the button horizontally */

  }
  
  /* Ensures proper height and alignment for legends that are not clickable */
  .legend {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    text-align: center;
    padding: 5px 0;
    width: 90%;
    margin-top: 70px;
    background: rgba(0, 0, 0, 0.5); /* Non-clickable background */
    color: white;
    border-radius: 4px;
  }
  
  
  .download-button:hover {
    margin-top:20px;
    background-color: #45a049;
  }
  
  /* Modal styling for enlarging images */
  .image-modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 1000;
    bottom: 1000;
    background-color: rgba(71, 47, 47, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100000;
    cursor: pointer;
  }
  .modal-content {
    background: white;
    border-radius: 8px;
    padding: 70px;
    height:60%;
    margin-top: 100px;
    max-height: 100%; /* Prevent the modal from exceeding screen height */
    overflow-y: auto; /* Enable vertical scrolling */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    width: 80%;
    max-width: 800px; /* Adjust as needed */
    min-height: 200px; /* Adjust as needed */
    position: absolute;

  }
  .image-modal:hover {
    cursor: pointer;
  }
  .enlarged-image {
    max-width: 100%;
    max-height: 100%;
    border-radius: 5px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5);
    animation: fadeIn 0.3s ease-in-out;
    object-fit: contain;
    margin: auto;
    z-index: 10000000;    
  }
  .vector1 {
    position: absolute;
    overflow-y: hidden;
    margin-top: 2920px; /* Distance from the bottom of the screen */
    margin-left: 896px;  /* Distance from the right edge of the screen */
    width: 1450px;
    max-width: 500%;
    overflow: hidden;
    height: auto; /* Maintain aspect ratio */
    object-fit: contain; /* Make the image scale proportionally */
    z-index: 2; /* Ensure it stays on top of other elements */
  }
  .vector3{
    position: absolute;
    overflow-y: hidden;
    margin-top: 260px;
    top: 220px; /* Distance from the bottom of the screen */
    margin-right: 921.5px;  /* Distance from the right edge of the screen */
    width: 1450px;
    max-width: 500%;
    height: auto; /* Maintain aspect ratio */
    object-fit: contain; /* Make the image scale proportionally */
    z-index: -1; /* Ensure it stays on top of other elements */
  }
  .portfolio-page-vector2{
    position: relative; /* Position the image inside the section */
