@import url('https://fonts.googleapis.com/css2?family=Gitan+Latin:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Shuttle+Block&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Venice+Blvd&display=swap');


.video-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1; /* Make sure the video stays behind other content */
  overflow-y: hidden;
}
.background-video {
  object-fit: cover; /* Ensure the video covers the whole screen */
  width: 100%;
  height: 100%;
}
.App-header {
  background-color: #e100ff;
  min-height: 100vh;
  display: flex;
  flex-direction: center;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  z-index: 1px;
}
.header{
  z-index: 2;
}
.App-link {
  color: #61dafb;
}.home-page-content {
  display: flex;
  flex-direction: column; /* Stack elements vertically */
  align-items: center; /* Align all items in the center horizontally */
  padding-top: 20px; /* Adjust padding to space out the top */
  margin-top: -200px;
}

.description {
  text-align: center;
  margin-bottom: 5px; 
  z-index: 50;
  background-color: rgb(84, 0, 85);
  border-radius: 300px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border: 2px solid #3498db; /* Border for the description */
  outline: 4px solid #fffdfd; /* Outer outline around the border */
   /* Slight shadow for depth */
  /* Rounded corners */


}

.profile-container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px; /* Space between profile and buttons */
}

.profile-picture {
  margin-top: 250px; 
  width: 150px; /* Adjust the width of the profile picture */
  height: 150px; /* Adjust the height of the profile picture */
  border-radius: 50%; /* Make the image circular */
}

.button-container {
  display: flex;
  justify-content: center;
  gap: 15px; /* Space between buttons */
}



.header-button:hover {
  background-color: #555; /* Hover effect for buttons */
}
.bottom-nav-buttons {
  width: 100px; /* Set width of the circle */
  height: 100px; /* Set height of the circle */
  background: linear-gradient(to bottom, #6a0dad, #7b015b);
  border-radius: 50%; /* Make the box circular */
  border: 2px solid white; /* White border outline */
  display: flex; /* Use flexbox for center alignment of content */
  justify-content: center; /* Horizontally center content inside the box */
  align-items: center; /* Vertically center content inside the box */
  color: white; /* Text color inside the box */
  font-family: 'Shuttle Block', sans-serif;
  font-weight: 300;
  font:bold;
  font-size: 14px; /* Optional: adjust font size */
}

.background-section3 {
  background: linear-gradient(180deg, #eba4ff, #550451); /* Light gray gradient */
  border: 0px solid rgb(131, 92, 92);
  height: 107%;
  min-height: 115vh;
  width: 100%;
  margin-top: -793px;
  min-width: 245.9vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #333;
  padding: 20px;
  box-sizing: border-box;
  border-radius: 10px;
  z-index: 2;
  overflow-x: hidden;
}

.bottom-nav-buttons-container {
  display: flex; /* Use flexbox to align the boxes horizontally */
  justify-content: space-evenly; /* Space the boxes evenly horizontally */
  align-items: center; /* Align items vertically */
  padding: 20px; /* Optional: adds padding around the container */
  z-index: 200;
}

/* Hover effect for the buttons */
.bottom-nav-buttons:hover {
  background-color: #aa29b9; /* Darker blue on hover */
  transform: scale(1.1); /* Slight scale effect on hover */
}

/* Active effect when button is clicked */
.bottom-nav-buttons:active {
  background-color: #861f91; /* Even darker blue when active */
  transform: scale(0.95); /* Slightly shrink the button when clicked */
}
.vector {
display: none;
}
.vector2{
display: none;
}
html,body {
  height: 100%; /* Make sure html and body cover the full height */
  width: 100%;
  padding: 0;
}
.cover-up{
  background: linear-gradient(180deg, #eba4ff, #550451); /* Light gray gradient */
  border: 0px solid rgb(131, 92, 92);
  height: 107%;
  min-height: 95vh;
  width: 100%;
  min-width: 245.9vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #333;
  padding: 20px;
  box-sizing: border-box;
  border-radius: 0px;
  z-index: -1200;
  overflow-x: hidden;

}
.navboxes{
  width: 100px; /* Set width of the circle */
  height: 100px; /* Set height of the circle */
  background-color: #1d0031; /* Dark purple background color */
  border-radius: 50%; /* Make the box circular */
  border: 2px solid white; /* White border outline */
  display: flex; /* Use flexbox for center alignment of content */
  justify-content: space-evenly; /* Horizontally center content inside the box */
  align-items: center; /* Vertically center content inside the box */
  color: white; /* Text color inside the box */
  font-size: 18px; /* Optional: adjust font size */
}
/* Responsive Adjustments */
@media (max-width: 1024px) { /* Tablets */
  .video-background {
    height: 400px;
    position: relative; /* Adjust for content stacking */
  }

  .App-header {
    flex-direction: column;
    text-align: center;
    padding: 20px;
    font-size: calc(8px + 1.5vmin);
  }

  .home-page-content {
    padding: 15px;
  }

  .description {
    padding: 10px;
    font-size: 16px;
    border-radius: 20px;
  }

  .profile-picture {
    width: 80px;
    height: 120px;
    margin-top: 100px;
  }

  .bottom-nav-buttons {
    width: 80px;
    height: 80px;
    font-size: 12px;
  }

  .background-section2,
  .background-section3 {
    min-height: 100vh;
    width: 100%;
    padding: 10px;
  }

  .vector,
  .vector2 {
    display: none; /* Hide vectors for phones */

  }
}

@media (max-width: 692px) { /* Phones */
  .header {
    width: 100%;
    min-width: 200vh;
    height: 100%;
    overflow-y: hidden;
    overflow-x: hidden;
    padding: 10px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 7;
  }
  .video-background {
    height: auto;
    position: relative;
    overflow: hidden;
  }

  .App-header {
    flex-direction: column;
    text-align: center;
    padding: 10px;
    font-size: calc(7px + 1.5vmin);
  }

  .home-page-content {
    padding: 10px;
  }
  .home-page4{
    margin-top: 0px;
    left: 0px;
  }

  .description {
    padding: 34px;
    font-size: 70px;
    font-style: normal;
    border-radius: 15px;
  }

  .profile-picture {
    width: 200px;
    height: 200px;
    margin-top: 80px;
  }

  .button-container {
    flex-direction: column;
    gap: 10px;
  }

  .bottom-nav-buttons {
    width: 120px;
    height: 120px;
    font-size: 10px;
  }
  .background-section3{
    height: 100%;
    min-height: 380vh;
    margin-top: -1700px;
    z-index: 3;
  }
  .cover-up{
display: inline;  }
  


  .background-section3 {
    height: 100%;
    min-height: 500vh;
    width: 120%;
    min-width: 30vh;
    margin-left: -50px;
    padding: 5px;
    overflow-x: hidden;
    overflow-y: hidden;
    z-index: 2000000;
  }

  .vector{
    margin-top: 200px;

  }
  .vector2 {

  }

  .navboxes {
    width: 120px;
    height: 80px;
    font-size: 14px;
  }
}
