body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background: linear-gradient(to bottom, #6a0dad, #ff69b4);
    width: 100%;
    min-width: 200vh;
    color: white;
  }
  
  /* Page Title */
  #socials-container h1 {
    text-align: center;
    font-size: 36px;
    margin: 20px 0;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
  }
  
  /* Social Grid */
  .socials-grid {
    margin-left: 200px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    padding: 20px;
  }
  
  /* Social Card */
  .social-card {
    background: #fff;
    color: #333;
    width: 300px;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    text-align: center;
  }
  
  /* Social Header */
  .social-header {
    font-size: 24px;
    margin-bottom: 10px;
    color: #6a0dad;
  }
  
  /* Social Images */
  .social-images {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    gap: 10px;
    border-radius: 50%;
    cursor: pointer;
    margin-bottom: 10px;
  }
  
  /* Profile Photo */
  .profile-photo {
    margin-top: 150px;
    margin-left: 250px;
    min-width: 15vh;   
    min-height: 15vh;   
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 2px solid #6a0dad;
    transition: transform 0.3s ease;
    cursor: pointer;
  }
  
  .profile-photo:hover {
    transform: scale(1.1);
  }
  
  /* Social Logo */
  .social-logo {
    margin-top: 150px;
    width: 100px;
    height: 100px;
  }
  
  /* Social Link */
  .social-link {
    margin-top: 30px;
    display: inline-block;
    margin-top: 90px;
    font-size: 24px;
    text-decoration: none;
    background-color: #dd75c4;
    padding: 10px;
    border-radius: 20px;
    color: #6a0dad;
    font-weight: bold;
    transition: color 0.3s ease;
  }
  
  .social-link:hover {
    color: #ff69b4;
  }
  .socials-page {
    margin-top: 00px;
    padding: 20px;
    padding-right: 70px;
    margin-left: 50px;
    padding-bottom: 20px;
    text-align: center;
    color: white;
    background: linear-gradient(180deg, #eba4ff, #550451); /* Light gray gradient */
    margin-top: -60px;
    min-height: 120vh;
    min-width: 120vh;
    width: 100%;
    height: 102%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow-y: hidden;
    overflow-x: hidden;
    z-index: 200;
  }
  .socials-page h1{
    margin-top: 0px;
    font-family: 'Shuttle Block', sans-serif; /* Matches the font family */
    font-style: normal; /* Remove italic styles if present */
    font-weight: bold; /* Matches the bold style */
    font-size: 39px; /* Matches the font size */
    color: hwb(0 100% 0%); /* Matches the text color */
    border: 1px dashed rgb(250, 188, 223);    
    box-shadow: 0 0 5px rgba(243, 164, 255, 0.5); /* Shadow around the header */
    padding: 15px;
    margin-top: 180px;
    margin-bottom: -30px;
    border-radius: 10px;
    z-index: 200;
  }
  .socials-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    height: 100%;
    min-height: 70vh;
    border-color: #ffffff;
    border-width: 5px;
    gap: 20px;
  }
  .social-card {
    background: linear-gradient(180deg, #ea9fff, #90046b); /* Light gray gradient */
    color: #ffffff;
    padding: 20px;
    width: 300px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    text-align: center;
    transition: transform 0.2s ease-in-out;
    z-index: 1;

  }
  .social-card:hover {
    transform: scale(1.05);
  }
  /* Interactive Button */
.interactive-button {
    display: inline-block;
    margin-top: 200px;
    padding: 10px 20px;
    color: white;
    background-color: #6a0dad;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease, transform 0.2s ease-in-out;
  }
  
  .interactive-button:hover {
    margin-top: 200px;
    background-color: #6f0668;
    transform: translateY(-3px);
  }
  /* Pagination */
  .pagination {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
  }
  
  .page-button {
    padding: 10px 15px;
    background-color: #6a0dad;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease-in-out;
  }
  
  .page-button:disabled {
    background-color: #aaa;
    cursor: not-allowed;
  }
  
  .page-button:hover:not(:disabled) {
    background-color: #ff69b4;
    transform: translateY(-3px);
  }
  
  .page-info {
    color: white;
    font-size: 16px;
  }
  html,body {
    height: 100%; /* Make sure html and body cover the full height */
    width: 100%;
    padding: 0;
  }
  .vector6 {
display: none
  }
  
  .vector7{
display: none;
  }
