@import url('https://fonts.googleapis.com/css2?family=Gitan+Latin:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Shuttle+Block&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Venice+Blvd&display=swap');

.gradient-header {
  background: linear-gradient(180deg, #a761d9, #30124d); /* Top to bottom gradient */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 1000px; /* Adjust header height as needed */
  text-align: center;
  position: relative;
  top: 0;
  left: 0;
  width: 200px;
  z-index: 400;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);

}

.gradient-header h1 {
  margin: 0;
  font-size: 28px;
  font-weight: bold;
  z-index: -500;

}

.nav-buttons {
  margin-top: 15px; /* Adds spacing between the title and buttons */
  font-family: "Arial", sans-serif;
}
.header-button {
  font-family: 'Shuttle Block', sans-serif; /* Use Shuttle Block font */
  font-size: 18px; /* Adjust font size as desired */
  font-weight: 1000; /* Shuttle Block supports normal weight */
  text-transform: uppercase; /* Optional: Make text uppercase */
  letter-spacing: 1px; /* Optional: Add spacing between letters */
  background: white;
  color: #6a0dad;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  cursor: pointer;
  transition: 0.3s ease, color 0.3s ease;
  margin: 5px;
  
}

.header-button:hover {
  background: #8a2be2;
  color: white;
}
.background-section {
  background: linear-gradient(180deg, #eba4ff, #550451); /* Light gray gradient */
  border: 5px solid white;
  height: 1000px;
  margin-top: -250px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #333;
  padding: 20px;
  box-sizing: border-box;
  border-radius: 10px;
  z-index: 200000;
}
.video-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1; /* Make sure the video stays behind other content */
}
/* Profile container layout */
.profile-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px;
}

/* Profile picture styling */
.profile-picture {
  width: 150px;
  height: auto;
  border-radius: 50%; /* Makes the image circular */
  border: 5px solid white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Adds shadow for depth */
  margin-bottom: 15px;
}

/* Description styling */
.description {
  font-family: 'Gitan Latin', 'Italic', sans-serif; /* Applies Gitan Latin font */
  max-width: 900px;
  text-align: center;
  margin-bottom: 20px;
  color: #ffffff; /* Adjust text color as needed */
  font-weight: 400;
}

.description h2 {
  font-family: 'Shuttle Block', sans-serif; /* Applies Shuttle Block font */
  font-style: normal; /* No italic style */
  font-weight: bold; /* Makes the text bold */
  font-size: 29px; /* Increased font size by 5px */
  margin: 0 0 10px 0;
  color: #ffffff; /* Adjust text color as needed */
}

.description p {
  font-family: 'Venice Blvd', sans-serif;
  font-size: 18px;
  font-weight: bold;
  line-height: 1.5;  
  font-weight: 600; /* Regular weight for paragraph */
  z-index: 1000;
  margin-top: 20px;
}

/* Text box buttons styled like navigation buttons */
.text-box-container {
  display: flex;
  gap: 15px; /* Adds spacing between buttons */
}

.header-button {
  font-family: 'Shuttle Block', sans-serif; /* Reuses Shuttle Block font */
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  background: white;
  color: #6a0dad;
  border: 2px solid #6a0dad;
  border-radius: 5px;
  padding: 10px 15px;
  cursor: pointer;
  transition: 0.3s ease, color 0.3s ease;
}

.header-button:hover {
  background: #8a2be2;
  color: white;
}
.image-page {
  text-align: center;
  padding: 50px;
  background: #f0f0f0;
  color: #333;
}

.image-page h1 {
  font-size: 32px;
  font-family: 'Shuttle Block', sans-serif;
  color: #6a0dad;
}

.image-page p {
  font-size: 18px;
  font-family: 'Gitan Latin', serif;
}


.nav-item {
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav-item2 {
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav-item3 {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* General Header Styling */
.header {
  width: 190%;
  min-width: 320vh;
  padding: 40px ;
  background: linear-gradient(to bottom, #6a0dad, #ff69b4);
  display: flex;
  flex-direction: column; /* Stack title above nav */
  justify-content: center;
  align-items: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: fixed;
  top: 0;
  overflow-x: hidden;
  overflow-y: hidden;
  position: absolute;
  z-index: 50000000000;
}



/* Centering Content */
.header-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 50;

}

/* Logo Styling */
.logo {
  width: 100px;
  margin-bottom: 0px;
}
.tooltip {
  position: absolute;
  bottom: -40px; /* Position below the navigation */
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 5px 10px;
  border-radius: 4px;
  font-size: 14px;
  white-space: nowrap; /* Prevent text wrapping */
  animation: fadeIn 0.3s ease-in-out;
  z-index: 10; /* Ensure tooltip appears above other elements */
}
/* Navigation Styling */
.nav {
  display: flex;
  gap: 20px;
  justify-content: center;
  justify-content: space-evenly;  /* Evenly distribute navigation buttons */
  align-items: center;
}

.nav-image {
  width: 60px;
  height: auto;
  margin: 5px;
  margin-bottom: -10px;
  margin-top: -10px;
  padding: 20px;
  transition: transform 0.2s ease;
}

.nav-image:hover {
  transform: scale(2);
  cursor: pointer;
}

.logo-link {
  margin-bottom: 20px; /* Space between the logo and the navigation */
}

.logo-image {
  width: 200px; /* Adjust size of the logo */
  height: auto; /* Maintain aspect ratio */
  justify-content: space-evenly;  /* Evenly distribute navigation buttons */
  margin-bottom: -20px;
  cursor: pointer;
}
 .home-page3 {
  height: 100%; /* Make sure html and body cover the full height */
  margin-top: 130px;
  margin-bottom: 130px;
  padding: 0;
  overflow-x: hidden;
  overflow-y: auto;
}
/* General styles (as provided by you) */
