@media (max-width: 692px) { /* Phones */
  .header {
    width: 100%;
    min-width: 200vh;
    padding: 10px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .nav{
    width: 80%;
    margin: 10px;
    height: 100px;
    margin-top: 40px;
  }
}